import { FC, useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import React from 'react';

import logo from '../assets/img/logo.png';
import { Modal } from '../components/modal/modal.tsx';
import { Pages } from '../pages/pages.ts';

import { Modals } from './modals.ts';

export const OnboardingModal: FC = (): JSX.Element => {
    const [disabled, setDisabled] = useState<number>(Date.now() + 1000);
    useEffect(() => {
        setTimeout(() => {
            setDisabled(0);
        }, disabled - Date.now());
    }, [disabled]);
    const navigate = useNavigate();
    const location = useLocation();

    const back = (): void => {
        navigate('/' + Pages.start, {
            state: {
                page: location
            }
        });
    };

    const next = (): void => {
        navigate('/' + Modals.rules, {
            state: {
                modal: location
            }
        });
    };

    return (
        <Modal id={Modals.onBoarding} header={' '}>
            <p>
                  <br />
                <img className="Page__logo" alt="" src={logo} />
                <br />
                &nbsp;    &nbsp;
                 ,   &nbsp;
                ? &nbsp; &nbsp;!
            </p>
            <div className="Modal__actions">
                <button className="Button--white" onClick={back}>
                    
                </button>
                <button
                    className="Button--blue"
                    onClick={next}
                    disabled={disabled > Date.now()}
                >
                    
                </button>
            </div>
        </Modal>
    );
};
